/* STATIC STYLES (desktop-first) */

#contact {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;

  background-color: var(--color-primary-darker);

  gap: 5rem;
}

.contact-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  width: min(50rem, 90vw);

  gap: 5rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .contact-cards {
    gap: 4rem;
  }
}

@media (max-width: 992px) {
  .contact-cards {
    gap: 3rem;
  }
}

@media (max-width: 768px) {
  .contact-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    width: 60vw;

    gap: 2rem;
  }

  .contact-cards > * {
    width: 100%;
  }
}

@media (max-width: 576px) {
  .contact-cards {
    width: 80vw;
  }
}
