/* STATIC STYLES (desktop-first) */

#experience {
  display: flex;
  flex-direction: column;

  width: 100%;

  background-color: var(--color-primary-darkest);

  gap: 5rem;
}

.experience-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));

  gap: 2rem;
}

.experience-cards p {
  display: flex;
  justify-content: center;

  margin-top: 0.5rem;

  gap: 0.5rem;
}

