/* STATIC STYLES (desktop-first) */

.timeline {
  position: relative;

  display: flex;
  flex-wrap: wrap;

  margin: auto;

  gap: 3rem;
}

.timeline::before {
  content: '';
  position: absolute;
  top: 5px;
  left: 50%;

  width: 5px;
  height: calc(100% - 10px);

  background-color: var(--color-secondary-dark);
}

.timeline-item {
  position: relative;

  width: 100%;
}

.timeline-item:not(:first-child) {
  margin-top: -10rem;
}

.timeline-item:nth-child(odd) {
  padding-right: calc(50% + 4rem);

  text-align: right;
}

.timeline-item:nth-child(even) {
  padding-left: calc(50% + 4rem);

  text-align: left;
}

.timeline-dot {
  position: absolute;
  top: 50%;
  left: calc(50% - 8px);

  width: 21px;
  height: 21px;

  border-radius: 50%;

  background-color: var(--color-secondary);
  box-shadow: 0 0 25px var(--color-secondary), 0 0 50px var(--color-secondary);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .timeline::before {
    left: 1rem;
  }

  .timeline-item:not(:first-child) {
    margin-top: 1rem;
  }

  .timeline-item:nth-child(odd) {
    padding-right: 0;

    text-align: left;
  }

  .timeline-item:nth-child(odd), .timeline-item:nth-child(even) {
    padding-left: 5rem;
  }

  .timeline-dot {
    left: 8px;
  }
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .timeline-item:nth-child(odd), .timeline-item:nth-child(even) {
    padding-left: 4rem;
  }
}

