/* STATIC STYLES (desktop-first) */

.socials {
  display: flex;

  margin-top: 2rem;

  gap: 1.5rem;
}

.socials a {
  color: var(--color-primary-text-dark);

  transition: 0.5 ease-in-out;
}

.socials a:hover {
  color: var(--color-secondary);

  transform: scale(1.3);
}

