/* STATIC STYLES (desktop-first) */

#services {
  display: flex;
  flex-direction: column;

  width: 100%;

  background-color: var(--color-primary-darkest);

  gap: 5rem;
  padding-inline: 15rem;
}

.services-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));

  gap: 4rem;
}

.services-cards > div {
  margin: 0 fit-content;
}

.services-cards h3 {
  margin-bottom: 1rem;
}

.services-cards p {
  margin-block: .5rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  #services {
    padding-inline: 12rem;
  }
}

@media (max-width: 992px) {
  #services {
    padding-inline: 10rem;
  }
}

@media (max-width: 768px) {
  #services {
    padding-inline: 8rem;
  }
}

@media (max-width: 576px) {
  #services {
    padding-inline: 2rem;
  }
}
