/* STATIC STYLES (desktop-first) */

#portfolio {
  display: grid;
  flex-direction: column;

  width: 100%;

  background-color: var(--color-primary-darker);

  gap: 5rem;
}

.portfolio-card-p {
  margin-block: .5rem;
}

.portfolio-image-container {
  margin: 0;
}

.portfolio-image {
  height: 20rem;

  object-fit: contain;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .portfolio-image {
    height: 19rem;
  }
}

@media (max-width: 992px) {
  .portfolio-image {
    height: 14rem;
  }
}

@media (max-width: 768px) {
  .portfolio-image {
    height: 7rem;
  }
}

@media (max-width: 576px) {
  .image-container {
    margin: 0;
  }

  .portfolio-image {
    height: 5rem;
  }
}
