/* STATIC STYLES (desktop-first) */

#hero {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100vw;
  min-height: 100vh;

  gap: 10vw;
}

.hero-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero-container span {
  color: var(--color-secondary);
}

.animated-text {
  position: relative;

  display: inline-block;
}

.animated-text::before {
  content: '';

  animation: words 16s infinite;
}

@keyframes words {
  0%, 50% {
    content: 'frontend developerem';
  }
  51%, 100% {
    content: 'frontend designerem';
  }
}

.animated-text::after {
  content: '';
  position: absolute;
  right: -8px;

  width: calc(100% + 8px);
  height: 100%;

  border-left: 3px colid var(--color-secondary);

  background-color: var(--color-primary-darkest);

  animation: cursor 0.6s infinite, typing 40s steps(21) infinite;
}

@keyframes cursor {
  to {
    border-left: 2px solid var(--color-secondary);
  }
}

@keyframes typing {
  10%, 15%, 30%, 35%, 50%, 55%, 70%, 75%, 90%, 95% {
    width: 0;
  }
  5%, 20%, 25%, 40%, 45%, 60%, 65%, 80%, 85% {
    width: calc(100% + 8px);
  }
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  #hero {
    gap: 5vw;
  }

  .hero-container {
    align-items: center;

    text-align: center;
  }
}

@media (max-width: 992px) {
  #hero {
    flex-direction: column-reverse;

    padding-top: 2rem;

    gap: 5rem;
  }
}

